const ssList = rttpgParams.ssList;

function SocialShare() {

    return (
        <div className="rt-tpg-social-share">
            { ssList.includes( 'facebook' ) && <a href="#" className="facebook"><i className="fab fa-facebook-f" aria-hidden="true"></i></a> }
            { ssList.includes( 'twitter' ) && <a href="#" className="twitter"><i className="fab fa-twitter" aria-hidden="true"></i></a> }
            { ssList.includes( 'linkedin' ) && <a href="#" className="linkedin"><i className="fab fa-linkedin-in" aria-hidden="true"></i></a> }
            { ssList.includes( 'pinterest' ) && <a href="#" className="pinterest"><i className="fab fa-pinterest" aria-hidden="true"></i></a> }
            { ssList.includes( 'reddit' ) && <a href="#" className="reddit"><i className="fab fa-reddit-alien" aria-hidden="true"></i></a> }
            { ssList.includes( 'email' ) && <a href="#" className="email"><i className="fa fa-envelope"></i></a> }
        </div>
    );
}

export default SocialShare;
