const { useEffect, useState } = wp.element;
import { CssGenerator } from "../../utils/css/CssGenerator";
import apiFetch from "@wordpress/api-fetch";
import Inspector from "./inspector";
import GridLayouts from "./layouts/GridLayouts";

const Edit = ( props ) => {
    const { isSelected, attributes, setAttributes } = props;

    //all attribute
    const {
        prefix, uniqueId, post_type, post_id, exclude, post_limit, offset, author, post_keyword, relation, orderby, order, display_per_page, layout_style,
        ignore_sticky_posts, no_posts_found_text, show_taxonomy_filter, show_pagination, media_source, image_size, image_offset_size, default_image, category_position,
        category_source, tag_source, taxonomy_lists, start_date, end_date, hover_animation, excerpt_type, excerpt_limit, excerpt_more_text, page, query_change, acf_data_lists,
        show_acf, cf_hide_empty_value, cf_show_only_value, cf_hide_group_title
    } = attributes;

    const [ posts, setPosts ] = useState( [] );
    const [ queryEffect, setQueryEffect ] = useState( false );
    const [ acfData, setAcfData ] = useState( [] )
    const [ imageSizes, setImageSizes ] = useState( [] )

    const handleQueryChange = () => {
        setQueryEffect( !queryEffect );
        setAttributes( { query_change: true, page: 1 } )
    }

    const fetch_all_posts = () => {
        apiFetch( {
            path  : '/rttpg/v1/query',
            method: 'POST',
            data  : {
                prefix, post_type, post_id, exclude, post_limit, offset, show_pagination, ignore_sticky_posts, orderby, order, display_per_page, layout_style, author,
                start_date, end_date, media_source, image_size, image_offset_size, show_taxonomy_filter, relation, post_keyword, no_posts_found_text, default_image,
                category_position, taxonomy_lists, category_source, tag_source, hover_animation, excerpt_type, excerpt_limit, excerpt_more_text, page, acf_data_lists,
                show_acf, cf_hide_empty_value, cf_show_only_value, cf_hide_group_title
            }
        } ).then( ( data ) => {
            setAttributes( { query_change: false } )
            setPosts( data )
            console.log(data)
        } );
    }
    const fetch_all_acf_data = () => {
        apiFetch( { path: "/rttpg/v1/acf" } )
            .then( acfData => {
                let acfArr = [];
                Object.keys( acfData ).map( key => {
                    acfArr.push( acfData[key] );
                } )
                setAcfData( acfArr )
            } )
    }

    const fetch_all_image_size = () => {
        apiFetch( { path: "/rttpg/v1/image-size" } )
            .then( ( imageSizes ) => {
                setImageSizes( imageSizes )
            } )
    }

    useEffect( () => {
        fetch_all_posts();
        fetch_all_acf_data();
    }, [ queryEffect, page ] );

    useEffect( () => {
        fetch_all_image_size();
    }, [] );

    if ( uniqueId ) {
        CssGenerator( attributes, 'tpg-grid-layout', uniqueId );
    }

    //render
    return [
        isSelected && (
            <Inspector
                attributes={ attributes }
                setAttributes={ setAttributes }
                changeQuery={ handleQueryChange }
                acfData={ acfData }
                imageSizes={imageSizes}
            />
        ),
        <GridLayouts props={ props } postData={ posts }/>
    ]

}
export default Edit;
