const { __ } = wp.i18n;
import { SelectControl, PanelBody } from "@wordpress/components";
import icons from "../../components/icon/icons";
import RangeDevice from "../../components/RangeDevice";
import { GRID_STYLE } from "../../components/Constants";
import Alignment from "../../components/Alignment";
import Layouts from "../../components/Styles";

function LayoutController( props ) {
    const { attributes, setAttributes } = props.data;
    //All attribute
    const { prefix, grid_column, grid_layout_style, ignore_sticky_posts, title_alignment } = attributes;
    let postLayout = prefix + "_layout";

    return (
        <PanelBody title={ __( 'Layout', 'the-post-grid' ) } initialOpen={ true }>
            <Layouts value={ attributes[postLayout] }
                     onChange={ val => props.changeLayout( val ) }
                     options={ [
                         { value: 'grid-layout1', svg: icons.grid_layout1, label: __( 'Layout 1' ) },
                         { value: 'grid-layout3', svg: icons.grid_layout2, label: __( 'Layout 2' ) },
                         { value: 'grid-layout4', svg: icons.grid_layout3, label: __( 'Layout 3' ) },
                         { value: 'grid-layout2', svg: icons.grid_layout4, label: __( 'Layout 4' ) },
                         { value: 'grid-layout5', svg: icons.grid_layout5, label: __( 'Layout 5' ) },
                         { value: 'grid-layout5-2', svg: icons.grid_layout6, label: __( 'Layout 6' ) },
                         { value: 'grid-layout6', svg: icons.grid_layout7, label: __( 'Layout 7' ) },
                         { value: 'grid-layout6-2', svg: icons.grid_layout8, label: __( 'Layout 8' ) },
                         { value: 'grid-layout7', svg: icons.grid_layout9, label: __( 'Layout 9' ) }
                     ] }
            />

            <RangeDevice
                label={ __( 'Grid Column', 'the-post-grid' ) }
                responsive={ true }
                value={ grid_column }
                min={ 0 }
                max={ 6 }
                step={ 1 }
                units={ false }
                onChange={ ( val ) => setAttributes( { grid_column: val } ) }
            />

            <SelectControl
                label={ __( 'Layout Style', 'the-post-grid' ) }
                help={ __( 'If you use card border then equal height will appear', 'the-post-grid' ) }
                className="rttpg-control-field label-inline rttpg-expand"
                value={ grid_layout_style }
                options={ GRID_STYLE }
                onChange={ ( grid_layout_style ) => {
                    setAttributes( { grid_layout_style } )
                } }
            />

            { grid_layout_style === 'masonry' && <small className="rttpg-help">{ __( "NB: Masonry will work only the front-end", "the-post-grid" ) }</small> }

            <Alignment
                label={ __( "Title Align", "the-post-grid" ) }
                value={ title_alignment }
                responsive={ true }
                options={ [
                    'left', 'center', 'right'
                ] }
                onChange={ val => setAttributes( { title_alignment: val } ) }
            />
        </PanelBody>
    );
}

export default LayoutController;
