const { __ } = wp.i18n;
import { PanelBody, } from "@wordpress/components";

function ACFStyle( props ) {
    const { attributes, setAttributes } = props.data;

    //All attribute
    const { prefix, show_acf } = attributes;
    let postLayout = prefix + "_layout";

    if ( !( show_acf === 'show' && attributes[postLayout] !== 'grid-layout7' ) ) {
        return '';
    }

    return (
        <PanelBody title={ __( 'Advanced Custom Field', 'the-post-grid' ) } initialOpen={ false }>


            {/* TODO: Have to emplement after ==============================================================*/ }


        </PanelBody>
    );
}

export default ACFStyle;
