const { Spinner } = wp.components;
const { useEffect } = wp.element;
import { get_parent_class_list } from "../../common/functions";

import Layout from "./grid-template/Layout";
import SectionTitle from "../../common/SectionTitle";
import PostPagination from "../../common/Pagination";

function GridLayouts( { props, postData } ) {
    const { attributes, setAttributes, clientId, className } = props;
    const {
        uniqueId, grid_layout, filter_btn_style, filter_type, grid_layout_style, display_per_page, page, query_change, show_pagination, pagination_type,
        load_more_button_text
    } = attributes;

    const posts = postData.posts;
    const totalPages = Math.ceil( postData.total_post / display_per_page );

    let dynamicParentClass = get_parent_class_list( { attributes, className } );
    const newClintID = clientId.substr( 0, 6 );

    useEffect( () => {
        if ( !uniqueId ) {
            setAttributes( { uniqueId: newClintID } );
        } else if ( uniqueId && uniqueId !== newClintID ) {
            setAttributes( { uniqueId: newClintID } );
        }
    }, [] );

    let is_carousel = '';
    if ( rttpgParams.hasPro && 'carousel' === filter_btn_style && 'button' === filter_type ) {
        is_carousel = 'carousel';
    }

    // Wrapper Class
    let innerWrapperClass = grid_layout.replace( /-2/g, "" );
    innerWrapperClass += ' grid-behaviour';
    innerWrapperClass += ' ' + grid_layout_style;
    innerWrapperClass += ' grid_layout_wrapper';

    let checkLayout = [ 'grid-layout1', 'grid-layout3', 'grid-layout4' ];
    if ( 'masonry' === grid_layout_style && checkLayout.includes( grid_layout ) ) {
        innerWrapperClass += ' tpg-full-height';
    }

    if ( query_change ) {
        innerWrapperClass += ' tpg-editor-loading';
    }

    return (
        <div className={ `rttpg-block-postgrid rttpg-block-${ uniqueId + ' ' +dynamicParentClass }` }>

            <div className="rt-container-fluid rt-tpg-container tpg-el-main-wrapper clearfix">

                <div className={ `tpg-header-wrapper ${ is_carousel }` }>
                    <SectionTitle attributes={ attributes }/>

                    {/*{TODO: Should Emplement FrontEnd filter  here}*/ }
                </div>

                <div className={ `rt-row rt-content-loader ${ innerWrapperClass }` }>
                    {
                        ( posts && posts.length ) ?
                            posts.map( post => {
                                if ( post ) {
                                    if ( post.message ) {
                                        return <div className="message">{ post.message }</div>
                                    }

                                    return <Layout attributes={ attributes } post={ post }/>
                                } else return null
                            } )
                            :
                            <div className="rttpg-postgrid-is-loading">
                                { ( postData?.message && postData.message ) ? postData.message : <Spinner/> }
                            </div>
                    }
                </div>
                <PostPagination props={ props } totalPages={ totalPages }/>
            </div>

        </div>
    )

}

export default GridLayouts;