import GridLayout1 from "./GridLayout1";
import GridLayout2 from "./GridLayout2";
import GridLayout3 from "./GridLayout3";
import GridLayout4 from "./GridLayout4";
import GridLayout5 from "./GridLayout5";
import GridLayout6 from "./GridLayout5";
import GridLayout7 from "./GridLayout5";
import GridLayout8 from "./GridLayout5";
import GridLayout9 from "./GridLayout5";

function Layout( { attributes, post } ) {
    const { grid_layout } = attributes;

    let layout = <GridLayout1 attributes={ attributes } post={ post }/>
    if ( 'grid-layout3' === grid_layout ) {
        layout = <GridLayout2 attributes={ attributes } post={ post }/>
    } else if ( 'grid-layout4' === grid_layout ) {
        layout = <GridLayout3 attributes={ attributes } post={ post }/>
    } else if ( 'grid-layout2' === grid_layout ) {
        layout = <GridLayout4 attributes={ attributes } post={ post }/>
    } else if ( 'grid-layout5' === grid_layout ) {
        layout = <GridLayout5 attributes={ attributes } post={ post }/>
    } else if ( 'grid-layout5-2' === grid_layout ) {
        layout = <GridLayout6 attributes={ attributes } post={ post }/>
    } else if ( 'grid-layout6' === grid_layout ) {
        layout = <GridLayout7 attributes={ attributes } post={ post }/>
    } else if ( 'grid-layout6-2' === grid_layout ) {
        layout = <GridLayout8 attributes={ attributes } post={ post }/>
    } else if ( 'grid-layout7' === grid_layout ) {
        layout = <GridLayout9 attributes={ attributes } post={ post }/>
    }

    return layout;
}

export default Layout;