import { get_el_thumb_cat } from "./functions";

function Thumbnail( { attributes, post } ) {
    const { prefix, category_position, show_category } = attributes;

    let postLayout = prefix + "_layout";

    let thumb_cat_condition = ( !( category_position === 'above_title' || category_position === 'default' ) );

    if ( attributes[postLayout] === 'grid-layout4' && category_position === 'default' ) {
        thumb_cat_condition = true;
    } else if ( thumb_cat_condition === 'default'
        && [ 'grid-layout4', 'grid_hover-layout11' ].includes( attributes[postLayout] ) ) {
        thumb_cat_condition = true;
    }

    return (
        <div className="rt-img-holder tpg-el-image-wrap">
            {
                ( ( rttpgParams.hasPro && show_category === 'show' && thumb_cat_condition && 'with_meta' !== category_position ) &&
                    get_el_thumb_cat( { attributes, post } )
                )
            }
            { post.image_url &&
                <img src={ post.image_url } className='rt-img-responsive' alt={ post.title }/>
            }

            <div className="overlay grid-hover-content"></div>
        </div>
    );
}

export default Thumbnail;
